
import { createClient, SupabaseClient } from '@supabase/supabase-js'
import type { Ebook } from '@/lib/data';
import type { ContactMessage } from '@/lib/messages';

// Define a type for our database
export type Database = {
  public: {
    Tables: {
      ebooks: {
        Row: Ebook;
        Insert: Omit<Ebook, 'id'>;
        Update: Partial<Ebook>;
      };
      messages: {
        Row: ContactMessage;
        Insert: Omit<ContactMessage, 'id' | 'date'>;
        Update: Partial<ContactMessage>;
      }
    };
    Views: {
      [_ in never]: never
    };
    Functions: {
      [_ in never]: never
    };
  };
};

let supabaseClient: SupabaseClient<Database> | null = null;

const getSupabase = () => {
    if (!supabaseClient) {
        const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
        const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY;

        if (!supabaseUrl || !supabaseAnonKey) {
            // This will be caught by the server if variables are not set.
            // On the client, it might result in a runtime error if not handled gracefully where used.
            throw new Error('Supabase URL and/or anon key are not defined in .env');
        }
        supabaseClient = createClient<Database>(supabaseUrl, supabaseAnonKey);
    }
    return supabaseClient;
};

// Export a singleton instance of the client
export const supabase = getSupabase();
