"use client";

import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Ebook } from "@/lib/data";
import { PlaceHolderImages } from "@/lib/placeholder-images";
import { useCart } from "@/context/cart-context";
import { ShoppingCart } from "lucide-react";

interface EbookCardProps {
  ebook: Ebook;
}

export function EbookCard({ ebook }: EbookCardProps) {
  const { addToCart } = useCart();
  const image = PlaceHolderImages.find((img) => img.id === ebook.coverImageId);

  return (
    <Card className="flex flex-col overflow-hidden transition-all hover:shadow-lg hover:-translate-y-1">
      <Link href={`/ebook/${ebook.id}`} className="flex flex-col flex-grow">
        <CardHeader className="p-0">
          <div className="relative aspect-[2/3] w-full">
            {image && (
              <Image
                src={image.imageUrl}
                alt={`Cover of ${ebook.title}`}
                data-ai-hint={image.imageHint}
                fill
                className="object-cover"
                sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              />
            )}
          </div>
        </CardHeader>
        <CardContent className="flex-grow p-4">
          <CardTitle className="mb-1 text-lg font-headline leading-tight group-hover:underline">
            {ebook.title}
          </CardTitle>
          <p className="text-sm text-muted-foreground">{ebook.author}</p>
        </CardContent>
      </Link>
      <CardFooter className="flex items-center justify-between p-4 pt-0">
        <p className="text-lg font-bold text-primary">
          GHâ‚µ{ebook.price.toFixed(2)}
        </p>
        <Button
          onClick={(e) => {
            e.stopPropagation();
            addToCart(ebook);
          }}
          size="sm"
          className="shrink-0"
        >
          <ShoppingCart className="h-4 w-4 md:mr-2" />
          <span className="hidden md:inline">Add</span>
        </Button>
      </CardFooter>
    </Card>
  );
}
